@use "colors";

* {
 -webkit-box-sizing: border-box;
 -moz-box-sizing: border-box;
 box-sizing: border-box;
}
/*
 * -- BASE STYLES --
 * Most of these are inherited from Base, but I want to change a few.
 */
html {
 scroll-padding-top: 4em;
 scroll-behavior: smooth;
}
body {
 line-height: 1.7em;
 color: colors.$dark_teal-color;
 font-size: 14px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
 color: colors.$teal-color;
 &:focus, &:target{
 border: 1px solid colors.$teal-color;
 }
}
a {
 color: colors.$teal-color;
 &[href^="http"]::after {
 content: "⎘";
 padding-left: 5px;
 }
}
hr{
 border-color:colors.$dark_teal-color;
}
/*
 * -- LAYOUT STYLES --
 * These are some useful classes which I will need
 */
 .off-screen {
 position: absolute;
 top: -2em;
 &:focus{
 top: 5px;
 color: white;
 z-index: 10;
 border: 1px white solid;
 background: #11111160;
 left: 30%;
 padding: 5px;
 }
 }
.content-box {
 padding: 1em;
}

.is-center {
 text-align: center;
}
.column-content{
 max-width: 800px;
	margin: auto;
}

.button {
 background-color: colors.$dark_teal-color;
 color: colors.$dark_white-color;
 padding: 0.5em 2em;
 border-radius: 5px;
 font-family: inherit;
 font-size: 100%;
 border: none transparent;
 text-decoration: none;
 display: inline-block;
 line-height: normal;
 white-space: nowrap;
 vertical-align: middle;
 text-align: center;
 cursor: pointer;
 user-select: none;
 box-sizing: border-box;
}
button {
 -webkit-appearance: button;
 text-transform: none;
 overflow: visible;
 margin: 0;
}



